/* -------------------------------------------- *
 * Toolbar
 */
.toolbar {
  margin-top: 10px;
  margin-bottom: 15px;
  padding: 5px 10px 0 10px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  background: #f4f4f4;
}

.toolbar:after {
  display: table;
  clear: both;
  content: '';
}

.pager-no-toolbar {
  margin-bottom: 10px;
}

.pager-no-toolbar ~ .pager-no-toolbar {
  margin-top: 10px;
}

.toolbar,
.pager {
  font-family: "Montserrat", "Helvetica Neue", Verdana, Arial, sans-serif;
  font-size: 12px;
  line-height: 30px;
  color: #413c38;
}

.toolbar label,
.pager-no-toolbar label {
  font-weight: normal;
  text-transform: uppercase;
}

.sorter {
  float: left;
  margin-bottom: 5px;
}

.sorter label {
  float: left;
  margin-right: 5px;
}

.sorter label:after {
  content: ':';
}

.sorter > .sort-by {
  float: left;
  height: 30px;
  margin-right: 5px;
}

.sorter > .sort-by .sort-by-switcher {
  display: inline-block;
  width: 30px;
  height: 30px;
}

.sorter > .sort-by .sort-by-switcher--asc {
  background-position: 4px -542px;
}

.sorter > .sort-by .sort-by-switcher--asc:hover {
  background-position: -46px -542px;
}

.sorter > .sort-by .sort-by-switcher--desc {
  background-position: 4px -567px;
}

.sorter > .sort-by .sort-by-switcher--desc:hover {
  background-position: -46px -567px;
}

.sorter > .view-mode {
  float: right;
}

.sorter > .view-mode .grid,
.sorter > .view-mode .list {
  float: left;
  width: 30px;
  height: 30px;
}

.sorter > .view-mode .grid {
  margin-right: 5px;
  background-position: 8px -492px;
}

.sorter > .view-mode strong.grid,
.sorter > .view-mode a.grid:hover {
  background-position: -42px -492px;
}

.sorter > .view-mode .list {
  background-position: 11px -517px;
}

.sorter > .view-mode strong.list,
.sorter > .view-mode a.list:hover {
  background-position: -39px -517px;
}

.pager {
  float: right;
  overflow: hidden;
}

.pager > .count-container {
  float: left;
}

.pager .amount {
  float: left;
  margin: 0 15px 0 0;
  font-family: "Helvetica Neue", Verdana, Arial, sans-serif;
  white-space: nowrap;
}

.pager .limiter {
  float: left;
  height: 30px;
  line-height: 30px;
}

.pager .limiter > label {
  padding-right: 5px;
}

.pager .limiter > label:after {
  content: ':';
}

.pager .amount,
.pager .limiter,
.pager .pages {
  margin-bottom: 5px;
}

.pages {
  float: right;
  overflow: hidden;
  margin-left: 15px;
}

.pages strong {
  display: none;
}

.pages li {
  float: left;
}

.pages a,
.pages .current {
  display: inline-block;
  width: 25px;
  height: 30px;
  padding: 0;
  font-family: "Helvetica Neue", Verdana, Arial, sans-serif;
  font-size: 13px;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-transform: uppercase;
  color: #39c;
  border: 0;
}

.pages .current,
.pages .current:hover {
  width: 30px;
  cursor: default;
  color: #413c38;
  border: 1px solid #ccc;
  background-color: #fff;
}

.pages .next,
.pages .previous {
  position: relative;
  font: 0/0 a;
  color: transparent;
  background-color: transparent;
  text-shadow: none;
}

.pages .next:hover,
.pages .previous:hover {
  border: 1px solid #39c;
}

.pages .next:before {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  content: '';
  border-top: 4px solid transparent;
  border-right: none;
  border-bottom: 4px solid transparent;
  border-left: 4px solid #39c;
  top: 50%;
  left: 50%;
  margin-top: -3px;
  margin-left: -2px;
}

.pages .next:hover:before {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  content: '';
  border-top: 4px solid transparent;
  border-right: none;
  border-bottom: 4px solid transparent;
  border-left: 4px solid #246b8f;
}

.pages .previous:before {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  content: '';
  border-top: 4px solid transparent;
  border-right: 4px solid #39c;
  border-bottom: 4px solid transparent;
  border-left: none;
  top: 50%;
  left: 50%;
  margin-top: -3px;
  margin-left: -2px;
}

.pages .previous:hover:before {
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  content: '';
  border-top: 4px solid transparent;
  border-right: 4px solid #246b8f;
  border-bottom: 4px solid transparent;
  border-left: none;
}

.toolbar .view-mode > label {
  display: none;
}

