/* ============================================ *
 * FORMS - COMMON
 * ============================================ */
/* -------------------------------------------- *
 * Fieldsets
 */
.fieldset p {
  margin-bottom: 7px;
}

.fieldset p.required {
  float: right;
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 12px;
}

.fieldset + .fieldset {
