  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 28px;
  text-transform: uppercase;
  color: #413c38;
  text-rendering: optimizeSpeed;
  margin-bottom: 15px;
  padding-bottom: 7px;
  border-bottom: 1px solid #ededed;
}

/* -------------------------------------------- *
 * Input Box
 */
.input-box {
  padding-top: 2px;
}

.input-box:after {
  display: table;
  clear: both;
  content: '';
}

li.centinel-logos div.input-box img {
  display: inline;
  vertical-align: middle;
}

/* For adjacent select inputs. */
/* Example: credit card expiration month and year. */
.input-box .v-fix {
  float: left;
  max-width: 100%;
  margin-right: 5px;
}

/* -------------------------------------------- *
 * Labels
 */
label {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
  display: inline-block;
  font-family: "Montserrat", "Helvetica Neue", Verdana, Arial, sans-serif;
  font-size: 13px;
  font-weight: 700;
}

label.required:after,
span.required:after {
