/* ============================================ *
 * Slideshow
 * ============================================ */
.slideshow-container {
  display: block;
  position: relative;
  width: 100%;
  margin: 10px 0;
  border: 1px solid #ededed;
}

.slideshow-container .slideshow {
  position: relative;
  width: 100%;
  margin: 0;
  padding: 0;
  border: 10px solid #fff;
}

.slideshow-container .slideshow > li {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
}

.slideshow-container .slideshow > li:first-child {
  position: static;
  z-index: 100;
}

.slideshow-container .slideshow > li img {
  width: 100%;
  max-width: 100%;
}

.slideshow-pager {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
  overflow: hidden;
  position: absolute;
  z-index: 115;
  top: auto;
  bottom: 0;
  width: 100%;
  text-align: center;
}

.slideshow-pager span {
  display: inline-block;
  width: 44px;
  height: 44px;
  font-family: arial;
  cursor: pointer;
  color: #ddd;
}

.slideshow-pager span:before {
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  margin-top: -8px;
  margin-left: -8px;
  content: '';
  border-radius: 1px;
  background-color: #fff;
}

.slideshow-pager span:hover:before {
  background-color: #2e8ab8;
}

.slideshow-pager span.cycle-pager-active:before {
  background-color: #297aa3;
}

.slideshow-pager > * {
  cursor: pointer;
}

.slideshow-prev,
.slideshow-next {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -o-user-select: none;
  position: absolute;
  z-index: 110;
  top: 0;
  width: 15%;
  max-width: 80px;
  height: 100%;
  cursor: pointer;
  opacity: .6;
}

.slideshow-prev:before,
.slideshow-next:before {
  display: block;
  position: absolute;
  top: 50%;
  width: 0;
  height: 0;
  margin-top: -15px;
  font-size: 0;
  content: '';
  border-style: solid;
}

.slideshow-prev:hover,
.slideshow-next:hover {
  opacity: 1;
}

.slideshow-prev.disabled,
.slideshow-next.disabled {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=50);
  opacity: 0.5;
  cursor: default;
}

.slideshow-prev {
  left: 8px;
}

.slideshow-prev:before {
  top: 50%;
  left: 10px;
  border-width: 15px 15px 15px 0;
  border-color: transparent #fff transparent transparent;
}

.slideshow-prev:hover:before {
  border-color: transparent #2e8ab8 transparent transparent;
}

.slideshow-next {
  right: 8px;
}

.slideshow-next:before {
  top: 50%;
  right: 10px;
  border-width: 15px 0 15px 15px;
  border-color: transparent transparent transparent #fff;
}

.slideshow-next:hover:before {
  border-color: transparent transparent transparent #2e8ab8;
}

.cycle-caption {
  position: absolute;
  z-index: 110;
  right: 15px;
  bottom: 15px;
  color: white;
}

.cycle-overlay {
  position: absolute;
  z-index: 120;
  bottom: 0;
  width: 100%;
  padding: 15px;
  font-family: tahoma, arial;
  opacity: .5;
  color: white;
  background: black;
}

