/* ============================================ *
 * Pricing Conditions
 * ============================================ */

.price-box {
    .minimal-price-link {
        .label {
            font-family: $f-stack-special;
            font-size: $f-size-xs;
            text-transform: uppercase;
            color: $c-red;
        }
    }
}

.map-info {
    white-space: nowrap;
}

.catalog-product-view {
    .map-info {
        white-space: normal;
        > span {
            float: right;
            margin-bottom: 5px;
        }
        .price-box.map-info > a {
            float: right;
            width: 100%;
        }
    }
}
