/* ============================================ *
 * Account - Orders, Invoices, Credit Memos.
 * ============================================ */

@include bp (max-width, $bp-xsmall) {
    .sales-order-invoice,
    .sales-order-creditmemo,
    .sales-order-shipment {
        .sub-title {
            float: left;
            width: 100%;
            font-size: $f-size-l;
            text-align: center;
        }
        .order-links {
            width: 100%;
            text-align: center;
        }
    }
}
