@mixin loadingOverlay($opacity: .8) {
    &:before,
    &:after {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        content: '';
    }

    &:before {
        z-index: 2;
        opacity: $opacity;
        background-color: white;
    }

    &:after {
        z-index: 3;
        background-image: url('../images/opc-ajax-loader.gif');
        background-repeat: no-repeat;
        background-position: center;
    }

    &.loading {
        position: relative;

        &:before,
        &:after {
            display: block;
        }
    }
}
