/* ============================================ *
 * Contacts
 * ============================================ */

.contacts-index-index {
    .buttons-set {
        @include bp(max-width, $bp-small) {
            max-width: 330px;
            margin-left: 0;

            button {
                float: left;
                margin-left: 0;
            }

            p.required {
                float: right;
            }
        }

        max-width: 435px;
        margin-left: $trim;

        p.required {
            float: left;
        }
    }
}
